<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <div class="addCon">
        <b-button variant="warning" @click="addBarcode">+ Barcode</b-button>
        <b-modal ref="addBarcodeModal" hide-footer no-close-on-backdrop title="Barcode -- 添加">
          <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event)">
            <b-form-group id="add_barcode_nameGroup" label="Barcode名称:" label-size="sm" label-for="add_barcode_name">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_barcode_name" v-model.trim="addForm.barcode_name" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_index_seqi5Group" label="Index I5 序列:" label-size="sm" label-for="add_index_seqi5">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_index_seqi5" v-model.trim="addForm.index_seqi5" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_index_seqi7Group" label="Index I7 序列:" label-size="sm" label-for="add_index_seqi7">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_index_seqi7" v-model.trim="addForm.index_seqi7" required>
              </b-form-input>
            </b-form-group>
            <b-button type="submit" style="width:100%" variant="info">添加</b-button>
          </b-form>
        </b-modal>
      </div>
      <b-table bordered responsive hover :items="barcodeLists" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="outline-theme" @click="edit(row)">
           编辑
          </b-button>
          <b-button size="sm" class="mr-2" variant="danger" @click="openDeleteModal(row)">
           删除
          </b-button>
          <b-modal :ref="'deleteBarcodeModal'+row.index" title="Barcode -- 删除" ok-variant="info" cancel-variant="link" cancel-title="取消" ok-title="确定" @ok="deleteBarcode(row)">
            <p class="my-4" style="text-align:left">是否确定要删除 Barcode名称：<b>{{row.item.barcode_name}}</b> ？</p>
          </b-modal>
          <b-modal :ref="'editBarcodeModal'+row.index" hide-footer no-close-on-backdrop title="Barcode -- 编辑">
            <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event,row)">
              <b-form-group :id="'edit_barcode_nameGroup'+row.index" label="Barcode名称:" label-size="sm" :label-for="'edit_barcode_name'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_barcode_name'+row.index" v-model.trim="currentForm.barcode_name" required disabled>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_index_seqi5Group'+row.index" label="Index I5 序列:" label-size="sm" :label-for="'edit_index_seqi5'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_index_seqi5'+row.index" v-model.trim="currentForm.index_seqi5" required>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_index_seqi7Group'+row.index" label="Index I7 序列:" label-size="sm" :label-for="'edit_index_seqi7'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_index_seqi7'+row.index" v-model.trim="currentForm.index_seqi7" required>
                </b-form-input>
              </b-form-group>
              <b-button type="submit" style="width:100%" variant="info">提交</b-button>
            </b-form>
          </b-modal>
        </template>
      </b-table>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>
  export default{
    mounted(){
      
      this.getTableData()
      
    },
    data () {
      return {
        menus: [{
          text: 'Barcode管理',
          href: '#'
        }],
        addForm:{
          barcode_name:'',
          index_seqi5:'',
          index_seqi7:''
        },
        currentForm:{
          barcode_name:'',
          index_seqi5:'',
          index_seqi7:''
        },
        fields: {
          barcode_name:{
            label:'Barcode名称'
          },
          index_seqi5:{
            label:'Index I5 序列'
          },
          index_seqi7:{
            label:'Index I7 序列'
          },
          operate:{
            label:'操作',
            thStyle:{
              width:'150px'
            }
          }
        },
        tableEmptyText:'正在请求数据...',
        barcodeLists: [],
        currentPage:1,
        total:0
      }
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/barcode/list",qs.stringify({page:this.currentPage,count:10})).then(response=>{
          // console.log(response.data)
          this.barcodeLists=response.data.data
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
        })
      },
      addBarcode(){
        this.$refs.addBarcodeModal.show()
      },
      edit(row){
        if(this.currentForm.barcodeid!==row.item.barcodeid){
          for(var k in row.item){
            this.currentForm[k]=row.item[k]
          }
        }
        this.$refs['editBarcodeModal'+row.index].show()
      },
      openDeleteModal(row){
        this.$refs['deleteBarcodeModal'+row.index].show()
      },
      deleteBarcode(row){
        this.$http.post("/api/barcode/del",qs.stringify({barcodeid:row.item.barcodeid})).then(response=>{
          this.getTableData()
          this.$Loading.finish()
        })
      },
      onSubmit(evt,row){
        // this.$Loading.start()
        evt.preventDefault();
        if(row){
          this.$http.post("/api/barcode/edit",qs.stringify(this.currentForm)).then(response=>{
            this.getTableData()
            this.currentForm={
              barcode_name:'',
              index_seqi5:'',
              index_seqi7:''
            }
            this.$refs['editBarcodeModal'+row.index].hide()
            this.$Loading.finish()
          })
        }else{
          // 添加
          // console.log(this.addForm)
          this.$http.post("/api/barcode/add",qs.stringify(this.addForm)).then(response=>{
            this.getTableData()
            this.addForm={
              barcode_name:'',
              index_seqi5:'',
              index_seqi7:''
            }
            this.$refs.addBarcodeModal.hide()
            this.$Loading.finish()
          })
        }
      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../less/init.less';
.btn-sm{
  padding: .08rem .5rem !important;
}
.addCon{
  text-align: right;
  padding-right: 20px;
  margin-bottom:10px;
}
.paginationCon{
  overflow: hidden;
  .pagination{
    float: right;
  }
}

</style>


